<template>
  <div
    id="productModal"
    class="modal fade"
    tabindex="-1"
    aria-labelledby="productModalLabel"
    aria-hidden="true"
    ref="modal"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 id="productModalLabel" class="modal-title">
            <span v-if="action === 'create'"></span>
            <span v-else></span>
          </h5>
          <button
            type="button"
            class="btn-close bg-white"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body text-start text-dark">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-1">
                <div class="form-group">
                  <label for="imageUrl"></label>
                  <div class="input-group mb-1">
                    <input
                      @change="getImgData"
                      id="imageUrl"
                      type="file"
                      accept=".jpg, .png"
                      class="form-control"
                      placeholder=""
                      ref="imageUrl"
                    />
                    <button
                      @click="uploadImg"
                      :disabled="!imgData"
                      class="btn btn-sm btn-outline-success flex-shrink-0"
                      type="button"
                    >
                      
                    </button>
                  </div>
                </div>
                <img class="img-fluid" :src="tempProduct.imageUrl" />
              </div>
              <div v-if="Array.isArray(tempProduct.imagesUrl)">
                <div v-for="(item, key) in tempProduct.imagesUrl" :key="key" class="mb-1">
                  <div class="form-group">
                    <label for="imageUrl"></label>
                    <input
                      v-model="tempProduct.imagesUrl[key]"
                      type="text"
                      class="form-control"
                      placeholder=""
                    />
                  </div>
                  <img class="img-fluid" :src="item" />
                </div>
                <div
                  v-if="
                    !tempProduct.imagesUrl.length ||
                      tempProduct.imagesUrl[tempProduct.imagesUrl.length - 1]
                  "
                >
                  <button
                    @click="tempProduct.imagesUrl.push('')"
                    class="btn btn-outline-primary btn-sm d-block w-100"
                  >
                    
                  </button>
                </div>
                <div v-else>
                  <button
                    @click="tempProduct.imagesUrl.pop()"
                    class="btn btn-outline-danger btn-sm d-block w-100"
                  >
                    
                  </button>
                </div>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="form-group">
                <label for="title"></label>
                <input
                  v-model.trim="tempProduct.title"
                  id="title"
                  type="text"
                  class="form-control"
                  placeholder=""
                />
              </div>

              <div class="row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input
                    v-model.trim="tempProduct.category"
                    id="category"
                    type="text"
                    class="form-control"
                    placeholder=""
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="unit"></label>
                  <input
                    v-model.trim="tempProduct.unit"
                    id="unit"
                    type="text"
                    class="form-control"
                    placeholder=""
                  />
                </div>
              </div>

              <div class="row">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input
                    v-model.number="tempProduct.origin_price"
                    id="origin_price"
                    type="number"
                    min="0"
                    class="form-control"
                    placeholder=""
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    v-model.number="tempProduct.price"
                    id="price"
                    type="number"
                    min="0"
                    class="form-control"
                    placeholder=""
                  />
                </div>
              </div>
              <hr />

              <div class="form-group">
                <label for="description"></label>
                <textarea
                  v-model.trim="tempProduct.description"
                  id="description"
                  type="text"
                  class="form-control"
                  placeholder=""
                >
                </textarea>
              </div>
              <div class="form-group">
                <label for="content"></label>
                <textarea
                  v-model.trim="tempProduct.content"
                  id="content"
                  type="text"
                  class="form-control"
                  placeholder=""
                >
                </textarea>
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input
                    v-model="tempProduct.is_enabled"
                    id="is_enabled"
                    class="form-check-input"
                    type="checkbox"
                    :true-value="1"
                    :false-value="0"
                  />
                  <label class="form-check-label" for="is_enabled"></label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
            
          </button>
          <button
            @click="$emit('updateProduct', tempProduct)"
            type="button"
            class="btn btn-primary"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import modalMixin from '../mixins/modalMixin';

export default {
  props: ['product', 'action'],
  data() {
    return {
      imgData: null,
      tempProduct: { ...this.product },
    };
  },
  mixins: [modalMixin],
  methods: {
    getImgData(e) {
      const file = e.target.files[0];
      const formData = new FormData();
      formData.append('file-to-upload', file);
      this.imgData = formData;
      console.log(this.$refs);
    },
    uploadImg() {
      this.$http
        .post(
          `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`,
          this.imgData,
        )
        .then((res) => {
          if (res.data.success) {
            this.imgData = null;
            this.$refs.imageUrl.value = '';
            if (this.action === 'create') {
              this.tempProduct.imageUrl = res.data.imageUrl;
            } else if (this.action === 'edit') {
              this.tempProduct.imagesUrl.push(res.data.imageUrl);
            }
          } else {
            alert(res.data.message);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  watch: {
    product() {
      this.tempProduct = this.product;
      if (!this.tempProduct.imagesUrl) {
        this.tempProduct.imagesUrl = [];
      }
      if (!this.tempProduct.imageUrl) {
        this.tempProduct.imageUrl = '';
      }
    },
  },
};
</script>
